import Vue from "vue";
import VueRouter from "vue-router";
import home_view from "../views/home_view.vue";
import contact_view from "../views/contact_view.vue";
import galeria_view from "../views/galeria_view.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home_view",
    component: home_view
  },
  {
    path: "/contacto",
    name: "contact_view",
    component: contact_view
  }, {
    path: "/galeria",
    name: "galeria_view",
    component: galeria_view
  }
];

const router = new VueRouter({
  routes
});

export default router;
